.home {
    margin-top: 6rem;
    .container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      font-size: 32px;
      .content-about {
        margin-top: 4rem;
        margin-bottom: 4rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 2rem;
        align-items: center;
        .sub-title-about {
          color: var(--pink);
          text-transform: uppercase;
          letter-spacing: 0.1rem;
        }
        .title-about {
          color: var(--accent-color1);
        }
        .description-about {
          color: var(--accent-color3);
          width: 40rem;
          
        }
        
      }
      .image-container-about {
        position: relative;
        z-index: 10;
        .image {
          img {
          }
        }
        .ellipse-container {
          .ellipse {
            z-index: -1;
            position: absolute;
            height: 15rem;
            width: 15rem;
            filter: blur(100px);
          }
          .pink {
            top: 40%;
            right: 40%;
            background-color: orange;
          }
          .orange {
            bottom: 40%;
            left: 40%;
            background-color: lightseagreen;
          }
        }
      }
    }
  }