.crypto-elispe {
    margin-top: 6rem;
    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
        font-size: 12px;
        margin-bottom: 4rem;
        justify-content: center;
        .card {
            background-color: #222222;
            display: flex;
            gap: 2rem;
            justify-content: space-around;
            align-items: center;
            width: 48rem;
            height: 10rem;
        }
        .image-btcoin {
            margin-left: 1.5rem;
        }
        .card-header {
            color: white;
            font-size: 35px;
        }
        .crypto-descript {
            color: white;
            font-size: 15px;
        }
    }
} 