nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .brand-container {
      .brand {
        img {}
      }
    }
    .toggle-container {
      display: none;
    }
    .links-container {
      .links {
        list-style-type: none ;
        display: flex;
        gap: 4rem;
        li {
          a {
            color: var(--accent-color1);
            text-decoration: none;
          }
          &:last-of-type {
            a {
              color: var(--pink);
            }
          }
        }
      }
    }
  }